export default{
	data(){
		return{
			firstSection:[
				{
					index:0,
					title:'累计节省(元)'
				},
				{
					index:1,
					title:'待确认积分'
				}
			]
		}
	},
	methods:{
		async choose(){
			var pics = await this.chooseImgAndUpload({
				count:3
			});
			
			console.log(pics);
		},
		manage(){
			uni.navigateTo({
				url:'./jifenDes'
			})
		},
		clickThree(index){
			if(index==0){
				uni.navigateTo({
					url:'./commissionOrder'
				})
			}else if(index==1){
				uni.navigateTo({
					url:'./myAchieve'
				})
			}else if(index==2){
				uni.navigateTo({
					url:'./PromoteFee'
				})
			}
		},
		jump(fun){
			console.log(fun);

			if(fun.index ==0){
				uni.navigateTo({
					url:'./statisticData'
				})
			}else if(fun.index ==1){
				uni.navigateTo({
					url:'./myMember'
				})
			}else if(fun.index ==2){
				uni.navigateTo({
					url:'./myRebuyMember'
				})
			}else if(fun.index ==3){
				uni.navigateTo({
					url:'./MyOrder/myOrderList'
				})
			}
		},
		address(){
			uni.navigateTo({
				url:'./userManage'
			})
			// uni.navigateTo({
			// 	url:'./addAddress'
